@import "./variables.scss";

.flex {
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    row-gap: 16px;
}

#intro {
    height: 240px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 84px;

    h1{
        margin-bottom: 24px;
    }

    a {
        color: $primary-color;
    }
}

#articles {
    padding: 0 84px;
}

.article-card {
    display: flex;
    justify-content: space-between;
}

.article-img-wrap {
    height: 300px;
    aspect-ratio: 3 / 2; 
    background-image: url('./assets/cover-delta.png');
    background-size: cover;
    background-position: center;
}

.article-text {
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    h3 {
        font-weight: 500;
        margin-bottom: 8px;
    }
}

.article-tag {
    color: $primary-color;
}